/* ApplyForm.module.css */
.gridContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem; /* Adjust the gap to your preference */
}

.formSection {
  display: flex;
  flex-direction: column;
  gap: 1rem; /* Space between form elements */
}
.formContainer {
  display: flex;
  flex-direction: column;
}

.infoSection {
  display: flex;
  flex-direction: column;
  letter-spacing: 4px;
  text-align: center;
}

.infoBlock {
  margin-bottom: 2rem; /* Space between info blocks */
}

@media (max-width: 829px) {
  .gridContainer {
    grid-template-columns: 1fr;
  }
  .formSection {
    order: 2;
  }
  .infoSection {
    order: 1;
  }
}
